.overflow-scroll {
    overflow-y: scroll; }

.contact-card h6 {
    line-height: 0.9em; }

.contact-card {
    margin-bottom: 2.5em; }

.logo-parceiros {
    height: 3em;
    width: auto;
    margin: 0 1em; }

.sobre-s-left {
    border-bottom: 1px solid $pink; }

@media only screen and (max-width: 990px) {
    .col-scroll, .arquivo-scroll {
        min-height: 62vh;
        max-height: 62vh;
        overflow: scroll; } }

@media only screen and (min-width: 990px) {
    .col-scroll, .arquivo-scroll {
        min-height: 79vh;
        max-height: 79vh; }

    .sobre-section, .arquivo-scroll {
        overflow: scroll; }

    .sobre-s-left {
        border-right: 1px solid $pink;
        border-bottom: 0; }

    .festival {
        width: 58%; }

    .info-momento {
        width: 75%; } }
